.main-featured-box {
  border: 1px solid black;
  border-radius: 6px;
}

.main-featured-box > a {
  display: grid;
  grid-template-columns: 1fr 4fr;
  align-items: center;
  padding: 0.6em 0.8em;
}

.cont-box-text > h3 {
  margin-bottom: 0.5em;
}
.home-main > h1 {
  font-size: 2rem;
  padding: 0.2em 0;
}

.main-heading {
  text-align: center;
  grid-column: 1 / 3;
  padding: 0.5em;
  background-color: lightcoral;
}

.main-financial,
.main-featured,
.main-health {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: auto auto 1fr;
  margin-bottom: 50px;
  column-gap: 1em;
}

.main-featured-box {
  margin: 0.4em auto;
  max-width: 500px;
  background-color: white;
}

.box-text-summary {
  color: rgba(0, 0, 0, 0.8);
}

.main-subsection {
  margin: 1em 0;
  grid-column: 1 / 3;
}

svg {
  font-size: 2rem;
}
