@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600;800&family=Sofia+Sans:wght@300;500;700&display=swap");

*,
*::before,
*::after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  min-height: 100vh;
  font-family: "Poppins", monospace;
  background-color: whitesmoke;
}

.home {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.intro {
  margin-bottom: 2em;
}

.home-main {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 10px 30px;
  align-items: center;
}

footer {
  margin-top: auto;
  border-top: 1px solid;
  text-align: center;
  padding: 0.3em 0;
  font-size: 0.8rem;
}

.price-per-sqr-ft {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.price-per-sqr-ft-main {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.price-per-sqr-ft-calculator {
  border: 1px solid black;
  box-shadow: inset 0 0 24px -10px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  flex: 1;
  padding: 30px;
}

.cont-area {
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: auto 1fr;
  margin: 0.5em 0;
  max-width: 100px;
  row-gap: 0.5em;
}

#inp-area {
  grid-row: 2 / 3;
  max-width: 100px;
}

#area-measurement {
  grid-row: 2 / 3;
}

.loan {
  flex-direction: column;
  display: flex;
  min-height: 100vh;
}

.loan-main {
  flex: 1;
  display: grid;
  grid-template-rows: auto 1fr;
}

.cont-loan-calculator {
  display: flex;
  justify-content: center;
}

.loan-calculator {
  padding: 30px;
  border: 1px solid black;
  box-shadow: inset 0 0 24px -10px rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
}

.cont-currency > h4 {
  margin-bottom: 0.4em;
}

.cont-currency-btns {
  max-width: 200px;
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  align-items: center;
}

.currency {
  padding: 0.4em 0.8em;
  font-size: 1.2rem;
  max-height: 50px;
  background-color: white;
  border: none;
  outline: 1px solid gray;
}

.cont-amount-input {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
  margin: 0.5em 0;
}

.cont-amount-search {
  display: grid;
  grid-template-columns: auto 1fr;
  position: relative;
}

.cont-interest-rate {
  display: flex;
  flex-direction: column;
  gap: 0.5em;
}

.cont-duration {
  margin: 0.5em 0;
  display: flex;
  flex-direction: column;
}

.cont-duration > label {
  margin-bottom: 0.5em;
}

.cont-duration > input {
  margin-bottom: 0.5em;
}

.btn-calculate {
  align-self: flex-start;
  padding: 1em 2em;
  margin-top: 4em;
  font-size: 1.2rem;
  font-weight: bold;
  background-color: #3f37c9;
  color: white;
  border-radius: 8px;
}

.currency-converter {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.currency-converter-main {
  flex: 1;
  display: grid;
  grid-template-rows: auto 1fr;
}

.cont-currency-converter {
  padding: 30px;
  border: 1px solid black;
  box-shadow: inset 0 0 24px -10px rgba(0, 0, 0, 0.2);
}

.cont-conversion-money > h4 {
  margin: 0.5em 0;
}

.bottom {
  background-color: lightgray;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 0.5em;
  margin-top: 1em;
  padding: 1em;
}

h5 {
  font-size: 0.9rem;
  margin: 1em 0;
}

.cont-round-input {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
}

.btn-decimal-place {
  font-size: 1rem;
  padding: 0.4em 0.8em;
  background-color: white;
}

.btn-convert {
  cursor: pointer;
  align-self: flex-start;
  padding: 1em 2em;
  margin-top: 3em;
  font-size: 1rem;
  background-color: #3f37c9;
  color: white;
  border-radius: 8px;
  font-size: 1.2rem;
  font-weight: bold;
}

.cont-money-input > svg {
  padding: 0.2em;
}

.cont-money-input {
  display: grid;
  flex-direction: column;
  max-width: 120px;
  row-gap: 10px;
}

input {
  font-size: 1.2rem;
  padding: 0.6em 0.4em 0.6em 1.5em;
  border-radius: 8px;
}

@media screen and (max-width: 800px) {
  .main-financial,
  .main-featured,
  .main-health {
    display: flex;
    flex-direction: column;
  }

  .navbar-bottom {
    /* 
    display: none */
  }

  .hamburger {
    display: block;
  }

  .navbar-menu {
    display: none;
  }

  .main-featured-box {
    display: flex;
  }
  .main-heading {
    font-size: 1.4rem;
  }
}

input[type="number"] {
  max-width: 100px;
}

#inp-currency-from,
#inp-currency-to {
  max-width: 100px;
}

.loan,
.price-per-sqr-ft,
.currency-converter,
.cm-to-feet,
.feet-to-inches,
.mm-to-inches,
.cm-to-inches,
.feet-to-meters,
.grams-to-pounds,
.kilos-to-pounds,
.micrograms-to-grams,
.ounces-to-pounds,
.kilos-to-stone,
.micrograms-to-mg,
.milligrams-to-grams,
.kilojoules-to-calories,
.BMI,
.BMR {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  animation: pageLoad 200ms ease-out;
}

@keyframes pageLoad {
  0% {
    opacity: 0.5;
  }

  50% {
  }

  100% {
    opacity: 1;
  }
}
.cm-to-feet-main,
.feet-to-inches-main,
.mm-to-inches-main,
.cm-to-inches-main,
.feet-to-meters-main,
.grams-to-pounds-main,
.kilos-to-pounds-main,
.micrograms-to-grams-main,
.ounces-to-pounds-main,
.kilos-to-stone-main,
.micrograms-to-mg-main,
.milligrams-to-grams-main,
.kilojoules-to-calories-main {
  flex: 1;
}

h2 {
  margin: 0.5em 30px;
}

.cm-feet-converter,
.cm-to-inches-converter,
.cm-to-feet-converter,
.feet-to-inches-converter,
.feet-to-meters-converter,
.mm-to-inches-converter,
.grams-to-pounds-converter,
.kilos-to-pounds-converter,
.micrograms-to-grams-converter,
.ounces-to-pounds-converter,
.kilos-to-stone-converter,
.micrograms-to-mg-converter,
.milligrams-to-grams-converter,
.kilojoules-to-calories-converter,
.BMI-calculator,
.BMR-calculator {
  padding: 30px;
  border: 1px solid black;
  box-shadow: inset 0 0 24px -10px rgba(0, 0, 0, 0.2);
}

.cont-unit-input {
  max-width: fit-content;
  display: grid;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
}

.cont-conversion-unit > h4 {
  margin-bottom: 0.4em;
}

#inp-conversion-amount {
  margin: 1em 0;
}

.btn-input {
  font-size: 1.1rem;
  padding: 1em 2em;
  border-radius: 8px;
  background-color: #7209b7;
  color: white;
  font-weight: bold;
}

.btn-height-imperial-metric {
  padding: 1em 1.5em;
}

.amount-currency {
  position: absolute;
  top: 28%;
  left: 10px;
}

.modal {
  font-size: 2rem;
  padding: 30px;
  font-weight: bolder;
  letter-spacing: 0.2ch;
  position: absolute;
  border: 1px solid;
  left: 0px;
  right: 0px;
  bottom: 50%;
  top: 50%;
  display: flex;
  padding: 5em;
  justify-content: center;
  align-items: center;
  background-color: whitesmoke;
  box-shadow: 0 0 100vh 100vh rgba(0, 0, 0, 0.5);
}

.close {
  font-size: 2rem;
  cursor: pointer;
  position: absolute;
  top: 20px;
  right: 20px;
}

.hide {
  display: none;
}

.cont-height-metric-imperial {
  margin: 1em 0;
}

.cont-weight-metric-imperial {
  margin: 1em 0;
}

.btn-weight-metric-imperial {
  padding: 1em 1.5em;
}

#inp-weight,
#inp-height-primary,
#inp-height-secondary {
  margin-left: 1em;
}

button {
  font-size: 1rem;
  border: none;
  outline: 1px solid black;
}

.highlight {
  background-color: #7209b7;
  color: white;
}

.BMR,
.BMI {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.BMR-main,
BMI-main {
  display: flex;
  flex-direction: column;
}

.BMR-calculator {
}

.cont-sex {
  margin: 1em 0;
}

.cont-height {
  margin: 1em 0;
}

.cont-age {
  margin: 1em 0;
}

#inp-age {
  margin-left: 1em;
}

.btn-sex {
  padding: 1em 1.5em;
}

.description {
  padding: 30px;
}

.paragraph {
  font-weight: 600;
}
